@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.navbar {
    margin-top: 0;
    overflow: hidden;
    background: #fff;
    padding: 16px;
    width: 100%;
    border: 0;
    z-index: 9;
    height: 70px;
    display: flex;
    justify-content: space-between;
    flex-flow: row nowrap;
}

.navcontainer {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin: auto;
}



.containerMessages {
    width: 80%;
    height: 100%;
    padding-top: 5%;
    margin: auto;
    display: block;
}

@media screen and (max-width : 1200px) {
    .containerMessages {
        min-width: 350px;
        height: 90%;
        padding-top: 5%;
        margin: auto;
        display: block;
    }
}

.content {

    height: 80vh;
    background-color: #fff;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.3);
    width: 100%;
    background-color: #fff;
    box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.3);
}

.content__tabs__text {
    font-size: clamp(8px, 1vw, 14px);
    margin-right: 10%;
}

.content__messages {
    list-style: none;
}

.content__message hr {
    width: 100%;
    height: 1px;
}
*{
    margin : 0;
    padding : 0;
    outline : 0;
    box-sizing: border-box;
}
html,body, #root{
    height: 100vh;
    outline: none;
    background-image: url(/static/media/bg1.e334220e.jpg);
    background-position: center;
    background-size: cover;
}

:focus {outline:0 !important;}

body{
    background: #f3f3f3;
    font-family: Roboto, sans-serif;
    /* -webkit-font-smoothing: antialised; */
}

body, input, button {
    font-family: Roboto, sans-serif;
    outline: none;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

